.form{display:grid;place-items:center}.form_error{width:100%;padding:10px 0;text-align:center;margin-bottom:15px;color:#d3371d;background-color:#ebc8c4;border:1px solid #d3371d}.text_input_area{margin-bottom:15px}.text_input{display:flex;flex-direction:column;width:300px;height:30px;padding:0 10px;border:1px solid #333;border-radius:10px;outline:none;color:#fff;background-color:#50c878;transition:outline .2s linear,background-color .2s linear}.text_input:focus{outline:1px solid #333;background-color:#398c55}.text_input::placeholder{color:rgba(255,255,255,.7)}.text_label{font-size:20px}.checkbox_input{cursor:pointer}.checkbox{font-size:20px;cursor:pointer}.submit_place{margin-top:20px;width:100%}.submit{font-size:20px;line-height:1;color:#fff;border:1px solid #000;background-color:#50c878;padding:5px 20px;border-radius:15px;cursor:pointer;transition:background-color .2s linear}.submit:hover{background-color:#398c55}.error{display:inline-block;color:red;margin-top:5px}.error:before{content:"‚ùå ";font-size:10px;position:relative;top:-1px}